import React, { useState, useEffect } from 'react';
import moment from 'moment';
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Card, Table, Tabs, Tab, Jumbotron } from 'react-bootstrap';

function getPreviousWorkday(){
  let workday = moment();
  let day = workday.day();
  let diff = 1;  
  if (day === 0 || day === 1){ 
    diff = day + 2;  
  }
  return workday.subtract(diff, 'days');
}

function App() {
  const [issueCollections, setIssueCollections] = useState([])
  const [eventCollections, setEventCollections] = useState([
    {type: "PushEvent", events: [], name: "New Commits"},
    {type: "PullRequestEvent", events: [], name: "New Pull Requests"},
    {type: "CreateEvent", events: [], name: "New Repositories"},
    {type: "IssuesEvent", events: [], name: "New Issues"},
    {type: "IssueCommentEvent", events: [], name: "New Comments"}
  ])
  const [cardVisibility, setCardVisibility] = useState({})
  const [key, setKey] = useState('issues');

  useEffect(() => {
    let lastWeekday = getPreviousWorkday().format('YYYY-MM-DD')
    async function getSummary() {
      console.log(`https://raw.githubusercontent.com/learn-co-curriculum/curriculum-team/master/issues_output/${lastWeekday}/summary.txt`)
      let summary = await fetch(`https://raw.githubusercontent.com/learn-co-curriculum/curriculum-team/master/issues_output/${lastWeekday}/summary.txt`)
                            .then(raw => raw.text())
                            .then(text => text.split('\n'))

      summary = summary.filter(el => el !== "").map(el => ({[el.split(' ')[0]]: []}))
      
      for(let element in summary) {
        console.log(`https://raw.githubusercontent.com/learn-co-curriculum/curriculum-team/master/issues_output/${lastWeekday}/${Object.keys(summary[element])[0]}.txt`)
        fetch(`https://raw.githubusercontent.com/learn-co-curriculum/curriculum-team/master/issues_output/${lastWeekday}/${Object.keys(summary[element])[0]}.txt`)
          .then(raw => raw.text())
          .then(issues => {
            issues = issues.split('\n')
            issues = issues.filter(el => el !== "")
            let issueSet = []
            for(let issue in issues) {
              let issueData = issues[issue].match(/\[(.*?)\]/g)
              let url = issues[issue].match(/http(.*)/g)[0]
              let message = issues[issue].match(/\]:(.*?)http/g)[0]
              message = message.slice(3, message.indexOf(' http'))
              issueSet.push({
                timestamp: issueData[0].slice(1, issueData[0].length -1), 
                user: issueData[1].slice(1, issueData[1].length -1), 
                url,
                message
              })
            }
            issueSet = issueSet.sort((issueA, issueB) => parseInt(issueB.timestamp) - parseInt(issueA.timestamp))
            summary[element][Object.keys(summary[element])[0]] = issueSet
            
            setIssueCollections(summary)
            setCardVisibility(prevState => ({
              ...prevState,
              [Object.keys(summary[element])[0]]: true
            }))
            
          })
      }
    }

    function getEvents() {
      console.log(`https://raw.githubusercontent.com/learn-co-curriculum/curriculum-team/master/issues_output/${lastWeekday}/events.json`)
      fetch(`https://raw.githubusercontent.com/learn-co-curriculum/curriculum-team/master/issues_output/${lastWeekday}/events.json`)
        .then(raw => raw.json())
        .then(json => {
          let newEventCollections = eventCollections
          json.forEach(event => {
            let i = newEventCollections.findIndex(element => element.type === event.type)
            if(newEventCollections[i] && newEventCollections[i].events) newEventCollections[i].events.push(event)
          })
          setEventCollections(newEventCollections)
          for(let i in newEventCollections) {
            setCardVisibility(prevState => ({
              ...prevState,
              [newEventCollections[i].name]: true
            }))
          }
          
        })
    }

    getSummary()
    getEvents()
  }, [])

  return (
    <div className="App">
      <Jumbotron><h1>Curriculum Health Monitor</h1></Jumbotron>
      <Tabs activeKey={key} onSelect={k => setKey(k)}>
        <Tab eventKey="issues" title="Open Issues">
          <section>
            <h2>Open Issues</h2>
            {issueCollections.map(trackObj => {
              let name = Object.keys(trackObj)[0]
              return (
                <Card>
                  <Card.Title onClick={() => setCardVisibility(prevState => ({...prevState, [name]: !prevState[name]}))}><span>{name} {cardVisibility[name] ? "ᐳ" : "ᐯ"}</span><span>{trackObj[name].length}</span></Card.Title>
                  { cardVisibility[name] ? [] : (
                  <Card.Body>
                    <Table striped bordered hover size="sm">
                      <tbody>
                        { trackObj[name].map(issue => {
                          let date = new Date(0)
                          date.setUTCSeconds(issue.timestamp)
                          return (
                            <tr>
                              <td>{date.toDateString()}</td>
                              <td>{issue.message}</td>
                              <td><a href={issue.url}>{issue.url}</a></td>
                              <td><a href={`https://github.com/${issue.user}`}>{issue.user}</a></td>
                            </tr>
                          )
                        }) }
                      </tbody>
                    </Table>
                  </Card.Body>
                  )}
                </Card>
              )
            })}
          </section>
        </Tab>
        <Tab eventKey="events" title="Recent Events">
            <section>
              <h2>Recent Events</h2>
              {eventCollections.map(eventTypeObj => {
                let name = eventTypeObj.name
                return (
                  <Card>
                    <Card.Title onClick={() => setCardVisibility(prevState => ({...prevState, [name]: !prevState[name]}))}><span>{name} {cardVisibility[name] ? "ᐳ" : "ᐯ"}</span><span>{eventTypeObj.events.length}</span></Card.Title>
                    { cardVisibility[name] ? [] : (
                      <Card.Body>
                        <Table striped bordered hover size="sm">
                          <tbody>
                            {eventTypeObj.events.map(event => {
                              return (
                                <tr>
                                  <td>{event.name}</td>
                                  <td><a href={event.repo}>{event.repo}</a></td>
                                </tr>
                              )
                            })}
                          </tbody>
                        </Table>
                      </Card.Body>
                    )}
                  </Card>
                )
              })}
            </section>
        </Tab>
        <Tab eventKey="resources" title="Resources">
            <section>
              <h2>Resources</h2>
              <div className="videos">
                <div>
                  <h4>Using Ripgrep to Search for Curriculum Issues</h4>
                  <iframe title="Using Ripgrep to Search for Curriculum Issues" width="560" height="315" src="https://www.youtube.com/embed/owDib4HwWjg" frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
                </div>
                <div>
                  <h4>Closing a Quiz Issue that Couldn't be Replicated</h4>
                  <iframe title="Closing a Quiz Issue that Couldn't be Replicated" width="560" height="315" src="https://www.youtube.com/embed/sK0qjPzmD6c" frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
                </div>
                <div>
                  <h4>Fixing a Readme Typo</h4>
                  <iframe title="Fixing a Readme Typo" width="560" height="315" src="https://www.youtube.com/embed/34yCQYX_wnQ" frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
                </div>
                <div>
                  <h4>Resolving Two Kinds of 'learn submit' Issues</h4>
                  <iframe title="Resolving Two Kinds of 'learn submit' Issues" width="560" height="315" src="https://www.youtube.com/embed/_Kx_NpzZf6w" frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
                </div>
              </div>
            </section>
        </Tab>
      </Tabs>
    </div>
  );
}

export default App;
